import React from "react";
import { sortByName } from "../redux/actions";
import { useDispatch } from "react-redux";
import s from "./styles/FilterApi.module.css";

const SortByName = () => {
  const dispatch = useDispatch();
  return (
    <div>
      <h4>Sort Alphabetically</h4>
      <div className={s.buttons}>
        <div className={s.contBtn}>
          <button className={s.btn} onClick={() => dispatch(sortByName("AZ"))}>
            AZ
          </button>
        </div>
        <div className={s.contBtn}>
          <button className={s.btn} onClick={() => dispatch(sortByName("ZA"))}>
            ZA
          </button>
        </div>
        <div className={s.contBtn}>
          <button className={s.btn} onClick={() => dispatch(sortByName("ALL"))}>
            <svg
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 24 24"
              fill="currentColor"
              class="w-6 h-6"
            >
              <path
                fill-rule="evenodd"
                d="M3 6.75A.75.75 0 013.75 6h16.5a.75.75 0 010 1.5H3.75A.75.75 0 013 6.75zM3 12a.75.75 0 01.75-.75h16.5a.75.75 0 010 1.5H3.75A.75.75 0 013 12zm0 5.25a.75.75 0 01.75-.75h16.5a.75.75 0 010 1.5H3.75a.75.75 0 01-.75-.75z"
                clip-rule="evenodd"
              />
            </svg>
          </button>
        </div>
      </div>
    </div>
  );
};

export default SortByName;
